* {
   font-family: Arial, Helvetica, sans-serif;
}

body {
   padding: 60px;
}

input {
   font-size: 1.2rem;
   width: 100%;
}

.card-grid {
   display: flex;
   flex-direction: row;
   flex-wrap: wrap;
}

.card {
   align-content: center;
   border: 1px solid grey;
   border-radius: 6px;
   box-shadow: 2px 2px 7px black;
   margin-bottom: 10px;
   margin-right: 10px;
   overflow: hidden;
   width: 300px;
}

.card p {
   padding: 5px;
   text-align: center;
}

.card img {
   max-height: 200px;
   width: 300px;
}
